//Vueaxiosaxiosthis.http.get()post
Vue.prototype.http = axios

const router = new VueRouter({
  routes,
  // mode:'history',
  scrollBehavior(to,from,savedPosition){
    // return{x:0,y:100}
    // return{ selector:'.btn'}

    if(savedPosition){
      return savedPosition
    }else{
      return{ x:0, y:0}
    }
  }
})

